<template>
  <div>
    <v-header :isUserInfo="false" :isOther="true" />
    <div class="login-box">
      <Form class="login-panel" :model="userInfo" :rules="userRelur" ref="loginForm">
        <FormItem prop="userName">
          <Input type="text" v-model="userInfo.userName" placeholder="请输入手机号" />
        </FormItem>
        <FormItem prop="password">
          <Input type="password" v-model="userInfo.password" placeholder="请输入密码" />
        </FormItem>
        <Form-item>
          <Button type="primary" long @click="login">登录</Button>
        </Form-item>
        <p class="other clearfix">
          <router-link class="forget" to="/web/register">忘记密码</router-link>
          <router-link class="register" to="/web/register">免费注册</router-link>
        </p>
      </Form>
    </div>
    <v-footer />
  </div>
</template>
<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      pageFrom: '',
      userInfo: {
        userName: "",
        password: ""
      },
      userRelur: {
        userName: [
          {
            required: true,
            message: "请输入手机号",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: "请输入密码",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    ...mapActions(["setUserInfo"]),
    // 登录
    login() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.$api
            .login({
              mobile: this.userInfo.userName,
              password: this.userInfo.password
            })
            .then(res => {
              this.setUserInfo(res.data.data);
              if(this.pageFrom){

                this.$router.back();
              }else{
                this.$router.push('/web/home');
              }
            })
            .catch(err => console.log(err));
        }
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.pageFrom = from.name;
    });
  }
};
</script>
<style lang="less" scoped>
@import "~@/less/mixins.less";
.login-box {
  position: relative;
  height: 555px;
  background: url("~@/assets/images/ximg13.png") no-repeat center / cover;

  .bg {
    .blockImg();
  }

  .login-panel {
    .ycenter();
    right: 21.87%;
    width: 320px;
    padding: 25px;
    background-color: #fff;
    border: 1px solid @borderColor;
  }

  .other {
    font-size: 5px;

    .forget {
      float: left;
    }

    .register {
      float: right;
      color: @themeColor;
    }
    a:hover {
      color: @themeColor;
      text-decoration: underline;
    }
  }
}
</style>



